@import url('https://fonts.googleapis.com/css2?family=Inconsolata:wght@200;300&display=swap');
*{
    padding: 0;
    margin: 0;
    font-family: 'Inconsolata', monospace;
}
.ok{
    height: 20vh;
    width: 20vw;
    position: relative;
    top: 14vh;
    left: 6vw;
    border-radius: 50%;
    background-color: red;
    animation: ani 0.1s ease-in-out infinite;
}
.click{
    background-color: rgb(24, 27, 106);
    height: 1vh;
    width: 1vw;
    position: absolute;
    top: 8vh;
    left: 16vw;
}
@keyframes ani{
    0%{
        transform: rotate(0deg);
        height: 50vh;
    }
    50%{
        transform: rotate(300deg);
        width: 50vw;
        background-color: yellow;
    }
    25%{
        width: 100vw;
        height: 25vh;
        background-color: rgba(0, 255, 213, 0.823);
    }
    100%{
        transform: rotate(360deg);
        height: 50vh;
        width: 10000000vw;
        background-color: black;
        position: relative;
        right: 10vw;
        background-color: black;
    }
}
